$fontFamily: "Montserrat";
//+++++++++++Шрифт по умолчанию============================================
//</ШРИФТЫ>================================================================

//<ПЕРЕМЕННЫЕ>=============================================================
$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1210;

$bg: #161616;
$accent: #0b65fc;
$dark: #282828;
$main-text: #fff;

//</ПЕРЕМЕННЫЕ>============================================================

//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>=============================================
@import "help/null";

body {
	background-color: $bg;
	color: $main-text;
	font-family: $fontFamily, sans-serif;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	&._lock {
		overflow: hidden;
	}
}
//</ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>============================================

//<ОБОЛОЧКА>================================================================
.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
//</ОБОЛОЧКА>===============================================================

//</ОСНОВНАЯ СЕТКА>=========================================================
._container {
	max-width: $maxWidthContainer + px;
	padding: 0 15px;
	margin: 0 auto;
}
@media (min-width: 641px) {
	._container {
		max-width: 640px;
	}
}
@media (min-width: 961px) {
	._container {
		max-width: 960px;
	}
}

@media (min-width: 1025px) {
	._container {
		max-width: 1024px;
	}
}

@media (min-width: 1281px) {
	._container {
		max-width: 1210px;
	}
}

//</ОСНОВНАЯ СЕТКА>=========================================================

//<ЭЛЕМЕНТЫ>================================================================
.btn {
	display: inline-block;
	padding: 20px 55px;
	border-radius: 30px;
	border: 1px solid #0b65fc;
	color: #0b65fc;
	font-family: "Bebas-Neue";
	font-size: 20px;
	font-style: normal;
	font-weight: 700;
	line-height: 1.2;
}
.title_h1,
.title_h2,
.title_h3,
.title_h4,
.form-modal__title {
	color: #fff;
	font-family: "Bebas-Neue";
	font-size: 44px;
	font-weight: 700;
	line-height: normal;
}
@media (min-width: 1025px) {
	.title_h1,
	.title_h2,
	.title_h3,
	.title_h4,
	.form-modal__title {
		font-size: 64px;
	}
}

